.main-nav {
  display: flex;
  justify-content: space-between;
  padding: 35px 0;
  align-items: center;

  @include vp-1023 {
    padding: 30px 0;
  }

  @include vp-767 {
    padding: 20px 0;
  }
}

.main-nav__logo {
  display: block;
  align-self: flex-start;
}

.main-nav__logo img {
  @include vp-1023 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: auto auto auto auto;
  column-gap: 33px;
  row-gap: 15px;

  @include vp-1023 {
    column-gap: 23px;
  }

  @include vp-767 {
    display: none;
  }
}

.main-nav__list a {
  color: $color-alice-blue;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}

.main-nav__toggle {
  display: none;

  @include vp-767 {
    display: block;
    background-color: transparent;
    background-image: url("../img/sprite/icon-open.svg");
    background-repeat: no-repeat;
    width: 24px;
    height: 12px;
    border: none;
    padding: 10px;
    margin-right: 10px;
  }
}
